body {
 background-color: hsl(0, 100%, 74%);
 background-image: url('./images/bg-intro-mobile.png');
 margin: 0rem;
 padding: 0rem;
}
main {
 padding: 0rem;
 margin: 2rem;
 font-family: 'Poppins', sans-serif;
 color: hsl(247, 21%, 92%);
 margin-bottom: 0rem;
 margin-top: 0rem;
 overflow: hidden;
}

h1 {
 padding-left: 1rem;
 padding-right: 1rem;
}
.title {
 display: flex;
 flex-direction: column;
 text-align: center;
}

p.Offers {
 background-color: hsl(248, 32%, 49%);
 padding: 1rem;
 padding-left: 3rem;
 padding-right: 3rem;
 border-radius: 0.5rem;
 margin-bottom: 1.5rem;
 text-align: center;
 box-shadow: 0px 10px rgba(68, 68, 68, 0.253);
}

.OfferSpan {
 font-weight: 800;
}

.submit {
 background-color: hsl(154, 59%, 51%);
 color: white;
 border: none;
 box-shadow: 0px 4px hsla(154, 72%, 31%, 0.609);
 letter-spacing: 0.1rem;
}

.Terms {
 color: hsl(0, 100%, 74%);
}

.formContainer {
 display: flex;
 flex-direction: column;
 background-color: white;
 border-radius: 0.5rem;
}

section.formContainer {
 box-shadow: 0px 10px rgba(68, 68, 68, 0.253);
}

form {
 display: flex;
 flex-direction: column;
 align-items: center;
 background-color: white;
 padding: 1rem;
 border-top-left-radius: 0.5rem;
 border-top-right-radius: 0.5rem;
}

input {
 width: 90%;
 padding: 0.5rem;
 padding-top: 1rem;
 padding-bottom: 1rem;
 margin: 0.5rem;
 font-weight: 800;
 border-radius: 0.5rem;
 border-style: solid;
 border-color: hsl(0, 0%, 92%);
 text-indent: 1rem;
}

input:hover{
cursor: text;
}

input.submit {
 width: 17.84375rem;
 cursor: pointer;
}

input::placeholder {
 padding-left: 0rem;
}

footer {
 font-weight: 500;
 font-size: x-small;
 margin-top: -1.8rem;
 margin-left: 3.5rem;
 margin-right: 3.5rem;
 margin-bottom: 0.5rem;
 text-align: center;
}

div.error {
 display: flex;
 justify-content: flex-end;
 width: 17.84375rem;
 color: hsl(0, 100%, 74%);
 font-style: italic;
 font-size: small;
 font-weight: 600;
 padding: 0;
 margin-top: 0;
 margin-bottom: 0rem;
}

input.error {
 border-color: hsl(0, 100%, 74%);
 color: hsl(0, 100%, 74%);
}

input.error::placeholder {
 color: hsl(0, 100%, 74%);
}

input.error:focus {
 border-color: hsl(0, 100%, 74%);

 outline: none hsl(0, 100%, 74%);
}

.parent {
 position: relative;
}

.iconError {
 position: absolute;
 bottom: 1.35rem;
 left: 7rem;
}
.errorContent {
 margin: 0;
 padding: 0;
}

@media only screen and (min-width: 1440px) {
 main {
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: 100vh;
  grid-template-areas: 'title title form form';
  align-items: center;
 }
 .title {
  margin-left: 8rem;
  grid-area: title;
  text-align: left;
 }

 h1 {
  font-size: 3rem;
  margin: 0;
 }

 .description {
  padding-left: 1rem;
  padding-right: 2rem;
  font-weight: 500;
 }

 .form {
  grid-area: form;
  margin-right: 8rem;
  margin-left: 0rem;
 }

 div.error {
  width: 31.399375rem;
 }

 .iconError {
  left: 13rem;
 }
}
